.card {
    position: relative;
}

.card img {
    width: 100%;
    display: block;
    border: 2px solid #fff;
    border-radius: 6px; 
    
}   

/* Front of card - the picture */
.card .front {
    transform: rotateY(90deg);
    position: absolute; 
    transition: all ease-in 0.2s;
}

.flipped .front {
    transform: rotateY(0deg);
    transition-delay: 0.2s;
    transform-style: preserve-3d;
}   

/* Back of card*/

.card .back {
    transition: all ease-in 0.2s;
    transition-delay: 0.2s;
    background-color: rgb(255, 255, 255);
}

.flipped .back {
    transform: rotateY(90deg);
    transition-delay: 0s; 
}